@import "~@/assets/scss/production.scss";@import "~@/assets/scss/variables.scss";@import "~@/assets/scss/functions.scss";@import "~@/assets/scss/mixin.scss";
@include b(error) {
  min-height: 100%;
  overflow: auto;
  background: repeat-x top center;

  @include background-image(url(~@/views/common/img/404_bg.jpg));

  @include e(wrapper) {
    width: 1000px;
    margin: 60px auto 120px;
  }

  @include e(header) {
    height: 65px;
    line-height: 65px;
    text-align: center;
  }

  @include e(logo) {
    position: relative;
    display: inline-block;
    width: 190px;
    height: 65px;
    vertical-align: top;
    background: no-repeat center;

    @include background-image(url(~@/assets/img/common/logo190x65.png));
  }

  @include e(vertical-line) {
    display: inline-block;
    width: 1px;
    height: 25px;
    margin-left: 30px;
    vertical-align: middle;
    border-right: 1px dotted #343434;
  }

  @include e(content) {
    box-sizing: content-box;
    padding: 35px 0;
    margin-top: 70px;
    background-color: #0f0f0f;
    border: 1px solid #6c5832;
  }

  @include e(img) {
    float: left;
    width: 370px;
    height: 365px;
    vertical-align: top;
  }

  @include e(tips-box) {
    width: 560px;
    margin-left: 370px;
    font-size: 13px;
    color: #ccc;
  }

  @include e(title) {
    font-size: 20px;
  }

  @include e(tips) {
    margin-top: 5px;
    line-height: 20px;
  }

  @include e(email) {
    margin-top: 18px;
  }

  @include e(link) {
    text-decoration: underline;
  }

  @include e(button-wrapper) {
    margin-top: 20px;
  }

  @include e(button) {
    margin-right: 12px;

    @include button(116px);
    @include button-solid();
  }

  @include e(horizontal-line) {
    margin: 30px 0;
    border-top: 1px solid #222;
  }
}
